<template>
	<div class="container">
	    <v-back :title="'通行统计 - 通行记录（' + list.length + '条数据）'" />
		<div class="box">
		    <md-field>
		        <md-field-item title="选择日期" :content="selectorValue1" @click="showSelector1" arrow solid />
		        <md-field-item title="选择宿舍楼" :content="selectorValue2" @click="showSelector2" arrow solid />
				<md-field-item title="用户类型" :content="selectorValue" @click="showSelector" arrow solid />
		    </md-field>
		</div>
		<md-date-picker
		    ref="datePicker"
		    v-model="isSelectorShow1"
		    type="custom"
		    title="选择日期"
		    large-radius
		    :custom-types="['yyyy', 'MM','dd']"
		    :default-date="currentDate"
		    @confirm="onSelectorConfirm1" ></md-date-picker>
		<md-selector 
		v-model="isSelectorShow2" 
		:data="data2" 
		@confirm="onSelectorConfirm2" 
		min-height="320px" 
		max-height="320px" 
		title="选择宿舍楼" 
		okText="确认" 
		large-radius></md-selector>
		<md-selector
		    v-model="isSelectorShow"
		    :data="data"
		    min-height="320px"
		    max-height="320px"
		    title="搜索类型"
		    okText="确认"
		    large-radius
		    @confirm="onSelectorConfirm"
		    >
		</md-selector>
		<div class="list_box">
			<div class="list" v-for="(item, index) in list" :key="index" @click="itemShow(item)">
				<div class="flex">
					<img v-if="item.snap_photo || item.photo" :src="item.snap_photo || item.photo" alt="" @click.stop="imgsShow(item.snap_photo || item.photo)">
					<img v-else src="@/assets/student.png" alt="">
					<div>
						<div class="flex">
							<h4>{{item.student_name}}</h4>[{{usertypeText(item.usertype)}}] {{item.student_nbr}}&nbsp;
							<span v-if="item.usertype == 'S'" :class="statusText(item) == '在寝' ? 'active' : (statusText(item) == '未归' ? '' : 'noactive')">[{{statusText(item)}}]</span>
						</div>
						<div style="padding: 2vw 0;">通行通道：<span style="font-size: 3.6vw;font-weight: bolder;" :style="item.entry_exit_type == '1' ? 'color: #1acfc6;' : item.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;'">({{item.entry_exit_type == '1' ? '进' : item.entry_exit_type == '2' ? '出' : '未知'}})</span>{{item.snap_camera_name}}</div>
						<div>通行时间：{{item.entry_exit_time}}</div>
					</div>
				</div>
			</div>
		</div>
		
		<md-landscape v-model="showFullScreen" :full-screen="true">
		    <div style="text-align: left;padding: 4vw;font-size: 4vw;line-height: 6vw;">
		        <div style="margin-bottom: 4vw;">详细信息</div>
		        <div class="flex align_center" style="background-color: #1acfc6;box-sizing: border-box;padding: 2vw;border-radius: 1vw;" v-show="showData.usertype != 'I'">
		            <img v-if="showData.photo" :src="showData.photo" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;"  @click="imgsShow(showData.photo)">
		            <img v-else src="@/assets/student.png" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;padding-top: 2vw;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;">
					<div class="flex btns">
					    <div v-show="showData.usertype == 'S'">
					        <div @click="statusChange(1)">在寝</div>
					    </div>
					    <div v-show="showData.usertype == 'S'">
					        <div @click="statusChange(0)">未归</div>
					    </div>
					    <div v-show="showData.usertype == 'S'">
					        <div @click="statusChange">实习</div>
					    </div>
					    <div v-show="showData.usertype == 'S'">
					        <div @click="statusChange">请假</div>
					    </div>
						<div v-show="showData.usertype == 'S'">
						    <div @click="statusChange">毕业</div>
						</div>
					</div>
				</div>
				<div class="person_list_text">
					<div class="flex" v-show="showData.usertype == 'S'">
						当时状态：
						<div class="full_status">
							{{ personStatusText(showData) }} | 
							<span :class="statusText(showData) == '在寝' ? 'active' : (statusText(showData) == '未归' ? '' : 'noactive')">{{statusText(showData)}}</span>
						</div>
					</div>
					<div class="flex align_center" v-show="personStatusText(showData) != '正常'">请假/实习：{{aroundTime}} <md-icon name="id-card" size="lg" @click="imgsShow(showData.person_io_leave_practices[0].photo)"></md-icon></div>
					<div class="flex">抓拍图片：<img v-show="showData.snap_photo" style="width: 15vw;height: 15vw;" @click="imgsShow(showData.snap_photo)" :src="showData.snap_photo" alt=""></div>
					<div>姓名：{{showData.student_name}}[{{usertypeText(showData.usertype)}}]</div>
					<div v-show="showData.usertype != 'I'">学号/工号：{{showData.student_nbr}}</div>
					<div v-show="showData.usertype == 'S'">学院：{{showData.org_names}}</div>
					<div v-show="showData.usertype == 'S'">班级：{{showData.class_names || '--'}}</div>
					<div v-show="showData.usertype == 'S'">住宿信息：{{showData.building_name}}{{showData.dorm_nbr || '--'}}间{{showData.sys_user ? showData.sys_user.bed_nbr : '--'}}床</div>
					<div>当时通行：<span style="font-size: 3.6vw;font-weight: bolder;" :style="showData.entry_exit_type == '1' ? 'color: #1acfc6;' : showData.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;'">({{showData.entry_exit_type == '1' ? '进' : showData.entry_exit_type == '2' ? '出' : '未知'}})</span>{{showData.snap_camera_name}} {{showData.entry_exit_time}}</div>
					<div v-show="showData.usertype == 'S'">备注：{{showData.modify_in_dorm_status_logs && showData.modify_in_dorm_status_logs.length > 0 ? '在寝状态由【' + showData.modify_in_dorm_status_logs[0].modify_user_name + '】在【' + showData.modify_in_dorm_status_logs[0].modify_time +'】修正过' : '--'}}</div>
				</div>
		    </div>
		</md-landscape>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
	</div>
</template>

<script>
	import VeHistogram from 'v-charts/lib/histogram'
	import VeLine from 'v-charts/lib/line'
	import dayjs from 'dayjs'
	export default {
		name: 'traffic_list',
		data() {
			return {
				selectorValue1: '',
				selectorValue2: '',
				isSelectorShow1: false,
				isSelectorShow2: false,
				currentDate: new Date(),
				value1: '',
				value2: '',
				data2: [],
				list: [],
				selectorValue: '全部',
				selectorActive: '',
				isSelectorShow: false,
				data: [
					{
						value: '',
						text: '全部'
					},
					{
						value: 'S',
						text: '学生'
					},
					{
						value: 'T',
						text: '教师'
					},
					{
						value: 'V',
						text: '访客'
					},
					{
						value: 'Y',
						text: '黄名单'
					},
					{
						value: 'I',
						text: '陌生人'
					}
				],
				
				showFullScreen: false,
				showData: {},
				isViewerShow: false,
				imgs: [],
				
				aroundTime: '',
			}
		},
		methods: {
			usertypeText(type) {
				let usertypename = ''
				this.data.forEach(item => {
					if (item.value == type) {
						usertypename = item.text
					}
				})
				return usertypename
			},
			statusText(item) {
				if (item.modify_in_dorm_status_logs && item.modify_in_dorm_status_logs.length > 0) {
					if (item.modify_in_dorm_status_logs[0].in_dorm_status == 0) {
						return '未归'
					} else if (item.modify_in_dorm_status_logs[0].in_dorm_status == 1) {
						return '在寝'
					} else {
						return '未知'
					}
				} else {
					if (item.entry_exit_type == 1) {
						return '在寝'
					} else  if (item.entry_exit_type == 2) {
						return '未归'
					} else {
						return '未知'
					}
				}
			},
			personStatusText(item) {
				let text = '无记录'
				if (item.entry_exit_status != null) {
					if (item.entry_exit_status == '0') {
						text = '正常'
					}
				}
				if (item.person_io_leave_practices && item.person_io_leave_practices.length > 0) {
					this.aroundTime = dayjs(item.person_io_leave_practices[0].no_attend_starttime).format('YYYY-MM-DD') + ' ~ ' + dayjs(item.person_io_leave_practices[0].no_attend_endtime).format('YYYY-MM-DD')
					if (item.person_io_leave_practices[0].type == 1) {
						text = '请假'
					} else {
						text = '实习'
					}
				}
				return text
			},
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
			showSelector() {
			    this.isSelectorShow = true
			},
			onSelectorConfirm({text, value}) {
				this.selectorValue = text
				this.selectorActive = value
				this.getData()
			},
			//获取宿舍楼栋
			getFloor() {
			    this.$getL('/dorm/floor', {
					userId: JSON.parse(localStorage.getItem('user')).user_id
				}, (res)=>{
			        this.data2 = []
			        res.data.forEach((item, index) => {
			            this.data2.push({
			                value: item.buildingId,
			                text: item.buildingName
			            })
						if (this.value2 == item.buildingId) {
							this.selectorValue2 = item.buildingName
						}
			        })
			        this.getData()
				})
			},
			showSelector1() {
			    this.isSelectorShow1 = true
			},
			showSelector2() {
			    this.isSelectorShow2 = true
			},
			onSelectorConfirm1() {
			    this.value1 = this.$refs.datePicker.getFormatDate('yyyy-MM-dd')
			    this.selectorValue1 = this.$refs.datePicker.getFormatDate('yyyy-MM-dd')
			    this.getData()
			},
			 onSelectorConfirm2({text, value}) {
			    this.value2 = value
			    this.selectorValue2 = text
			    this.getData()
			},
			getData() {
			    //ajax todo
			    //获取通行人群
			    this.$getL('/traffic_statistics/people', {
			        userId: JSON.parse(localStorage.getItem('user')).user_id,
			        entryExitTime: this.value1,
			        buildingId: this.value2,
					usertype: this.selectorActive
			    }, (res)=>{
			        console.log(res.test)
					this.list = res.test
			        this.$toast.hide()
			    })
			},
			itemShow(item) {
				this.showData = item
				this.showFullScreen = true
				/* if ( item.usertype == 'S' ) {
					this.showData = item
					this.showFullScreen = true
				} */
			},
			statusChange() {
				//
			}
		},
		mounted() {
			this.selectorValue1 = dayjs(new Date(this.$route.query.entryExitTime)).format('YYYY-MM-DD')
			this.selectorActive = this.$route.query.usertype
			this.data.forEach(item => {
				if (item.value == this.$route.query.usertype) {
					this.selectorValue = item.text
				}
			})
			
			this.value1 = dayjs(new Date(this.$route.query.entryExitTime)).format('YYYY-MM-DD')
			this.value2 = this.$route.query.buildingId
			this.getFloor()
		}
	}
</script>

<style scoped="scoped">
	.list_box {
		padding: 4vw;
		background-color: #fff;
		font-size: 4vw;
		overflow-y: auto;
		height: calc(100vh - 63vw);
	}
	.list {
	    padding: 3vw;
	    margin-bottom: 4vw;
	    border: 1px solid #e2e4ea;
	}
	.list h4 {
		margin-right: 2vw;
	}
	.list img {
		width: 20vw;
		height: 23vw;
		margin-right: 4vw;
	}
	.list span.active {
	    color: #2f86f6;
	}
	.list span.noactive {
	    color: #ccc;
	}
	.list span {
	    color: #fdb731;
	}
</style>
